@import url("https://fonts.googleapis.com/css2?family=VT323&display=swap");

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    overflow: hidden;
    --background-color: #1c1e33;
    --primary-color: #ffffff;
    --fonts: "VT323", monospace;
    font-family: var(--fonts);
}

body {
    background-color: var(--background-color);
    color: var(--primary-color);
}

p {
    font-size: 24px;
    font-family: var(--fonts);
}

input {
    font-size: 24px;
    font-family: var(--fonts);
    background-color: var(--background-color);
    color: var(--primary-color);
    border: 3px solid var(--primary-color);
    padding: 4px;
}

.title-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 33%;
    font-size: 96px;
    font-family: var(--fonts);
}

.menu-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    gap: 15px;
}

.btn {
    margin: 10px;
    padding: 10px;
    border: 3px solid var(--primary-color);
    background-color: var(--background-color);
    color: var(--primary-color);
    font-size: 24px;
    font-family: var(--fonts);
    cursor: pointer;
    transition: all 0.2s ease-in-out;
}
.btn:hover {
    background-color: var(--primary-color);
    color: var(--background-color);
}

#map {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: calc(100% - 28px);
    gap: 2px;
    transition: clip-path 0.2s ease-in-out;
}

.row {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 2px;
}

.row > .cell {
    width: calc(75 / 21 * 1lvmin - 2px);
    height: calc(75 / 21 * 1lvmin - 2px);
    color: var(--primary-color);
    font-size: 24px;
    font-family: var(--fonts);
    cursor: pointer;
    transition: all 0.05s ease-in-out;
}

.wall {
    color: var(--background-color);
    border: 0px !important;
}

.well {
    background-color: #70cfff;
}

.save {
    background-color: #ffff5c;
}

div[class*=" spawn"] {
    background-color: green;
}

div[class*=" exit"] {
    background-color: purple;
}

div[class*=" floor"] {
    background-color: grey;
}

div[class*=" item"] {
    background-color: blue;
}

div[class*=" mobs"] {
    background-color: red;
}

.player {
    background-color: #ffffff !important;
}

#battle-screen {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 15%;
    transition: clip-path 0.2s ease-in-out;
    position: absolute;
    background-color: var(--background-color);
    width: 100%;
    height: 100%;
    z-index: 2;
    font-size: 24px;
}
.entity-container {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    gap: 25%;
}

.player-sprite {
    background-color: #ffffff;
    width: calc(75 / 21 * 2lvmin - 2px);
    height: calc(75 / 21 * 2lvmin - 2px);

    margin-top: 20%;
}

.enemy-sprite {
    background-color: red;
    width: calc(75 / 21 * 2lvmin - 2px);
    height: calc(75 / 21 * 2lvmin - 2px);

    margin-top: 20%;
}
.battle-log {
    border: white 2px solid;
    height: 25%;
    width: 75%;
    text-align: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 16px;
}
.level-up--container {
    display: flex;
    flex-direction: column;
    position: absolute;

    width: 75%;
    align-items: center;

    left: 12%;
    background: var(--background-color);
    z-index: 2;
}

.stat-container {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    gap: 5%;
}
